<template>
	<div>
		<Nav navtitle=''></Nav>
		<div class='wraperwidth'>
			<p class='titleex'>2005118XXXYZ500500</p>
			<div>
				<img class='imgsize' src='../../../images/index-network.jpg' />
				<div class='index-internet-content'>
					<div class='index-internet-content'>
						<!---->
						<div class='index-internet-box' v-for='(item,index) in internet' :key=index>
							<p class='index-internet-body-p1'>{{item.num}}</p>
							<p class='index-internet-body-p2'>{{item.introduction}}</p>
						</div>
						<div style='clear: both;'></div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Nav from '../common/nav.vue'
export default {
  data () {
    return {
      internet: [
        {
          num: '860+',
          introduction: '860'
        },
        {
          num: '125+',
          introduction: ''
        },
        {
          num: '1000+',
          introduction: '1000'
        },
        {
          num: 'TOP',
          introduction: ''
        }
      ]
    }
  },
  components: {
    Nav
  }
}
</script>

<style scoped>
.wraperwidth {
  width: 1420px;
  margin: 0 auto;
  position: relative;
}

.titleex {
  margin-top: 45px;
  font-size: 14px;
  color: #c2c2c2;
  position: relative;
}

.index-internet-body {
  width: 100%;
  position: relative;
}
.index-internet-content {
  position: absolute;
  width: 1420px;
  margin: 0 auto 0 auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.index-internet-box {
  width: 185px;
  padding: 150px 20px 0 20px;
  float: left;
  text-align: center;
  margin-left: 160px;
}

.index-internet-box:nth-of-type(1) {
  margin: 0;
}
.imgsize {
  width: 100%;
}
.in .index-internet-title-introduction {
  font-size: 14px;
  color: #707070;
  text-shadow: 0.4px 0.4px 0.4px #707070;
  margin-top: 20px;
}

.index-internet-body-p1 {
  font-size: 54px;
  color: #ee882a;
  font-weight: bolder;
}

.index-internet-body-p2 {
  font-size: 14px;
  color: #ee882a;
  text-shadow: 0.4px 0.4px 0.4px #ee882a;
  margin-top: 20px;
}
</style>



